<template>
  <div
    class="pa-5 d-flex"
    :class="{ 'flex-column': commonStore.isXsMobileMode }"
  >
    <v-card density="comfortable" class="ma-1 flex-grow-0 flex-shrink-1">
      <v-card-title class="bg-primary-light">攻略管理</v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <v-list nav>
          <v-list-item
            v-for="guide in guides"
            :key="guide.code"
            :title="guide.name"
            :subtitle="guide.code"
            :value="guide.code"
            active-color="secondary"
            @click="showEditor(guide.code)"
            :active="selectedGuide.classType == guide.code"
          >
            <template v-slot:prepend>
              <v-img
                :src="filePath.getClassImagePath(guide.icon)"
                width="30"
                max-width="30"
                class="rounded-sm me-3"
              ></v-img>
            </template>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
    <v-slide-x-transition>
      <GuideEditor
        v-if="selectedGuide.classType"
        class="ma-1 flex-grow-1 flex-shrink-0"
        :classType="selectedGuide.classType"
      />
    </v-slide-x-transition>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue';
import { useCommonStore } from '@/store/common';
import GuideEditor from '@/components/admin/GuideEditor.vue';
import { filePath } from '@/lib/filepath';

const guides = ref([
  {
    name: '風念禁咒魔導士',
    code: 'arch_mage_wind_ghost',
    icon: 'arch_mage.png',
  },
  {
    name: '火禁咒魔導士',
    code: 'arch_mage_fire',
    icon: 'arch_mage.png',
  },
  {
    name: '暗禁咒魔導士',
    code: 'arch_mage_shadow',
    icon: 'arch_mage.png',
  },
  {
    name: '謳歌大主教',
    code: 'cardinal_adoramus',
    icon: 'cardinal.png',
  },
  {
    name: '創世帝國聖衛軍',
    code: 'imperial_guard_genesis_ray',
    icon: 'imperial_guard.png',
  },
]);
const selectedGuide = ref({
  classType: '',
});

const commonStore = useCommonStore();

onMounted(async () => {
  await commonStore.updateLoadStatus(true);
});

function showEditor(classType) {
  selectedGuide.value.classType = classType;
}
</script>
<style lang=""></style>
