<template>
  <div class="pa-5">
    <v-row>
      <v-col cols="5">
        <v-card density="comfortable" class="mb-7">
          <v-card-title class="bg-primary-light">裝備管理</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <div class="mb-3">
              <v-btn
                variant="tonal"
                color="secondary"
                @click="showEditor('', '', false, false)"
              >
                <v-icon>mdi-plus</v-icon>
                新增
              </v-btn>
            </div>
            <v-card density="comfortable">
              <v-card-title class="bg-primary-light">搜尋</v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-text-field
                  label="裝備名稱"
                  v-model="name"
                  @update:modelValue="findItems"
                  density="comfortable"
                  prepend-inner-icon="mdi-magnify"
                ></v-text-field>
                <v-card-subtitle>搜尋結果</v-card-subtitle>
                <v-list nav>
                  <v-list-item
                    v-if="foundEquipments.length == 0"
                    class="text-overline"
                  >
                    目前無搜尋結果
                  </v-list-item>
                  <template
                    v-for="category in foundEquipments"
                    :key="category.categoryCode"
                  >
                    <v-list-subheader>
                      {{ category.category }}
                    </v-list-subheader>
                    <v-list-item
                      v-for="item in category.items"
                      :key="item.id"
                      :title="item.name"
                      :subtitle="item.id"
                      :value="item.id"
                      active-color="secondary"
                      @click="
                        showEditor(item.id, category.categoryCode, false, true)
                      "
                      :active="selectedItem.id == item.id"
                    >
                      <template v-slot:prepend>
                        <v-img
                          :src="
                            filePath.getCommentItemImagePath(
                              item.imgNames[0],
                              category.categoryCode,
                              false
                            )
                          "
                          width="30"
                          max-width="30"
                          class="rounded-sm me-3"
                        ></v-img>
                      </template>
                      <template v-slot:append>
                        <v-btn
                          color="error"
                          size="small"
                          @click.stop="
                            removeItem(item.id, category.categoryCode, false)
                          "
                          variant="tonal"
                        >
                          <v-icon>mdi-delete</v-icon>
                          刪除
                        </v-btn>
                      </template>
                    </v-list-item>
                    <v-list-item
                      v-for="card in category.cards"
                      :key="card.id"
                      :title="card.name"
                      :subtitle="card.id"
                      :value="card.id"
                      active-color="secondary"
                      @click="
                        showEditor(card.id, category.categoryCode, true, true)
                      "
                      :active="selectedItem.id == card.id"
                    >
                      <template v-slot:prepend>
                        <v-img
                          :src="
                            filePath.getCommentItemImagePath(
                              card.imgNames[0],
                              category.categoryCode,
                              true
                            )
                          "
                          width="30"
                          max-width="30"
                          class="rounded-sm me-3"
                        ></v-img>
                      </template>
                      <template v-slot:append>
                        <v-btn
                          color="error"
                          size="small"
                          @click.stop="
                            removeItem(card.id, category.categoryCode, true)
                          "
                          variant="tonal"
                        >
                          <v-icon>mdi-delete</v-icon>
                          刪除
                        </v-btn>
                      </template>
                    </v-list-item>
                  </template>
                </v-list>
              </v-card-text>
            </v-card>
          </v-card-text>
        </v-card>
        <v-card density="comfortable">
          <v-card-title class="bg-primary-light">輸出</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <div class="d-flex flex-row mb-3">
              <v-btn
                variant="tonal"
                color="secondary"
                @click="generateJson"
                class="me-2"
              >
                <v-icon class="me-1">mdi-code-json</v-icon>
                產生 JSON
              </v-btn>
              <v-btn variant="tonal" color="secondary" @click="copyJson">
                <v-icon class="me-1">mdi-content-copy</v-icon>
                複製 JSON
              </v-btn>
            </div>
            <v-textarea
              outlined
              label="JSON"
              v-model="json"
              readonly
              density="comfortable"
              hide-details="auto"
              class="my-3"
            ></v-textarea>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-slide-x-transition>
          <EquipmentEditor
            v-if="selectedItem.id || !selectedItem.editMode"
            :id="selectedItem.id"
            :categoryCode="selectedItem.categoryCode"
            :isCard="selectedItem.isCard"
            :editMode="selectedItem.editMode"
            @save="save"
          />
        </v-slide-x-transition>
      </v-col>
    </v-row>
  </div>
  <v-slide-y-transition>
    <v-alert
      v-if="alert.display"
      class="alert"
      type="success"
      density="compact"
      variant="tonal"
      closable
    >
      {{ alert.text }}
    </v-alert>
  </v-slide-y-transition>
</template>
<script setup>
import { ref, onMounted } from 'vue';
import { useEquipmentStore } from '@/store/equipment';
import { useCommonStore } from '@/store/common';
import EquipmentEditor from '@/components/admin/EquipmentEditor.vue';
import { filePath } from '@/lib/filepath';
import { logger } from '@/lib/logger';
import { common } from '@/lib/common';
import { v4 as uuidv4 } from 'uuid';
import { openDialog } from 'vue3-promise-dialog';
import ConfirmDialog from '@/components/admin/ConfirmDialog.vue';

const name = ref('');
const equipmentDb = ref({});
const foundEquipments = ref([]);
const json = ref('');
const alert = ref({
  display: false,
  type: 'success',
  text: '',
});
const selectedItem = ref({
  id: '',
  categoryCode: '',
  isCard: false,
  editMode: true,
});

const commonStore = useCommonStore();
const equipmentStore = useEquipmentStore();

onMounted(async () => {
  await commonStore.updateLoadStatus(true);
  await equipmentStore.fetchEquipmentTempDb();
  equipmentDb.value = common.deepClone(equipmentStore.equipmentTempDb);
});

function showEditor(id, categoryCode, isCard, editMode) {
  if (editMode) {
    selectedItem.value.id = id;
    selectedItem.value.categoryCode = categoryCode;
    selectedItem.value.isCard = isCard;
  } else {
    selectedItem.value.id = uuidv4();
    selectedItem.value.categoryCode = '';
    selectedItem.value.isCard = false;
  }
  selectedItem.value.editMode = editMode;
}

function findItems() {
  foundEquipments.value =
    equipmentStore.getEquipmentWithCategoryFromTempDbByName(name.value);
}

async function removeItem(id, categoryCode, isCard) {
  const confirm = await openDialog(ConfirmDialog, {
    message: '是否確定刪除?',
    type: 'error',
  });
  if (!confirm) {
    return;
  }
  let category = equipmentDb.value.equipments.find((category) => {
    return category.categoryCode == categoryCode;
  });

  if (!isCard) {
    category.items = category.items.filter((item) => item.id != id);
  } else {
    category.cards = category.cards.filter((card) => card.id != id);
  }

  equipmentDb.value.equipments.forEach((category) => {
    category.items.forEach((item) => {
      if (item.relatedIds) {
        item.relatedIds = item.relatedIds.filter((relatedId) => {
          return relatedId != id;
        });
      }
    });
    category.cards.forEach((card) => {
      if (card.relatedIds) {
        card.relatedIds = card.relatedIds.filter((relatedId) => {
          return relatedId != id;
        });
      }
    });
  });

  equipmentStore.updateEquipmentTempDb(equipmentDb.value);
  closeEditor();
  findItems();
  showAlert('刪除成功');
}

function save(item, originalRelatedIds, categoryCode, isCard, editMode) {
  let category = equipmentDb.value.equipments.find((category) => {
    return category.categoryCode == categoryCode;
  });
  item.relatedIds.forEach((id) => {
    let relatedItem = findItemById(id);
    if (!relatedItem.relatedIds) {
      relatedItem.relatedIds = [];
    }
    if (!relatedItem.relatedIds.includes(item.id)) {
      relatedItem.relatedIds.push(item.id);
    }
  });
  if (editMode) {
    if (originalRelatedIds) {
      originalRelatedIds.forEach((id) => {
        if (!item.relatedIds.includes(id)) {
          let relatedItem = findItemById(id);
          relatedItem.relatedIds = relatedItem.relatedIds.filter(
            (relatedId) => {
              return relatedId != item.id;
            }
          );
        }
      });
    }
  }
  if (!isCard) {
    category.items = category.items.filter((i) => {
      return i.id != item.id;
    });
    category.items.push(item);
  } else {
    category.cards = category.cards.filter((c) => {
      return c.id != item.id;
    });
    category.cards.push(item);
  }
  equipmentStore.updateEquipmentTempDb(equipmentDb.value);
  if (!editMode) {
    closeEditor();
  }
  showAlert('儲存成功');
}

function findItemById(id) {
  let item;
  equipmentDb.value.equipments.forEach((category) => {
    let foundItem = category.items.find((item) => {
      return item.id == id;
    });
    if (foundItem) {
      item = foundItem;
      return;
    }

    let foundCard = category.cards.find((card) => {
      return card.id == id;
    });
    if (foundCard) {
      item = foundCard;
      return;
    }
  });
  return item;
}

function closeEditor() {
  selectedItem.value = {
    id: '',
    categoryCode: '',
    isCard: false,
    editMode: true,
  };
}

var timeout;
function showAlert(text) {
  clearTimeout(timeout);
  alert.value.text = text;
  alert.value.display = true;
  timeout = setTimeout(() => {
    alert.value.display = false;
  }, 2000);
}

function generateJson() {
  json.value = equipmentStore.getEquipmentJsonFromTempDb;
}

function copyJson() {
  navigator.clipboard.writeText(json.value);
  showAlert('複製成功');
}
</script>
<style scoped>
.alert {
  position: fixed;
  top: 60px;
  left: calc(50vw - 120px);
}
</style>
