import { defineStore } from 'pinia';
import axios from 'axios';
import { logger } from '@/lib/logger';
import { common } from '@/lib/common';
import { useEquipmentStore } from '@/store/equipment';

export const useGuideStore = defineStore('guide', {
  state: () => {
    return {
      guide: {
        classType: '',
        title: '',
        introduction: [],
        build: {
          skill: {
            content: [],
            info: [],
          },
          state: {
            content: [],
          },
        },
        equipment: {
          content: [],
          comments: [],
        },
        video: {
          content: [],
          urls: [],
        },
      },
      guideTempDbList: [],
    };
  },
  getters: {
    getGuide: (state) => {
      return state.guide;
    },
    getGuideTempDb: (state) => {
      return (classType) => {
        return state.guideTempDbList.find((guideTempDb) => {
          return guideTempDb.classType == classType;
        })?.db;
      };
    },
    getGuideJsonFromTempDb: (state) => {
      return (classType) => {
        const guideDb = state.guideTempDbList.find((guideTempDb) => {
          return guideTempDb.classType == classType;
        })?.db;
        return generateJson(guideDb);
      };
    },
  },
  actions: {
    async fetchGuide(classType) {
      const equipmentStore = useEquipmentStore();
      await equipmentStore.fetchEquipmentTempDb();
      let responseEquipmentDb = equipmentStore.equipmentTempDb;

      let responseGuideDb = (
        await axios.get(
          `${process.env.BASE_URL}database/guide_${classType}.json`
        )
      ).data;
      let equipmentWithComments = [];
      responseGuideDb.equipment.comments.forEach((category) => {
        const targetCategory = responseEquipmentDb.equipments.find(
          (c) => c.categoryCode == category.categoryCode
        );

        let finalCategory = common.deepClone(targetCategory);

        finalCategory.items = mergeItemAndComment(
          category.items,
          targetCategory.items,
          finalCategory.items,
          category.categoryCode,
          false
        );

        finalCategory.cards = mergeItemAndComment(
          category.cards,
          targetCategory.cards,
          finalCategory.cards,
          category.categoryCode,
          true
        );

        equipmentWithComments.push(finalCategory);
      });

      logger.info(equipmentWithComments);
      responseGuideDb.equipment.comments = equipmentWithComments;
      responseGuideDb.classType = classType;
      this.guide = responseGuideDb;
    },
    async fetchGuideTempDb(classType) {
      if (
        this.guideTempDbList.find((guideTempDb) => {
          return guideTempDb.classType == classType;
        })
      ) {
        return;
      }
      const equipmentStore = useEquipmentStore();
      await equipmentStore.fetchEquipmentTempDb();
      let responseEquipmentDb = equipmentStore.equipmentTempDb;

      let responseGuideDb = (
        await axios.get(
          `${process.env.BASE_URL}database/guide_${classType}.json`
        )
      ).data;
      mergeItemAndCommentForTempDb(responseEquipmentDb, responseGuideDb);
      this.guideTempDbList.push({ classType: classType, db: responseGuideDb });
    },

    async updateGuideTempDb(classType, guideDb) {
      let guideTempDb = this.guideTempDbList.find((g) => {
        return g.classType == classType;
      });
      guideTempDb.db = guideDb;
    },
  },
});

function mergeItemAndComment(
  categoryItems,
  targetCategoryItems,
  finalCategoryItems,
  categoryCode,
  isCard
) {
  finalCategoryItems = [];
  categoryItems.forEach((item) => {
    let targetItem = common.deepClone(
      targetCategoryItems.find((e) => e.id == item.id)
    );
    targetItem.star = item.star;
    targetItem.comments = item.comments;
    targetItem.categoryCode = categoryCode;
    targetItem.isCard = isCard;
    targetItem.infos = [];
    finalCategoryItems.push(targetItem);
  });
  finalCategoryItems.sort((a, b) => b.star - a.star);
  return finalCategoryItems;
}

function mergeItemAndCommentForTempDb(responseEquipmentDb, responseGuideDb) {
  responseGuideDb.equipment.comments.forEach((categoryComment) => {
    const equipmentDbCategory = responseEquipmentDb.equipments.find(
      (categoryEquipment) => {
        return categoryEquipment.categoryCode == categoryComment.categoryCode;
      }
    );
    categoryComment.items.forEach((itemComment) => {
      const itemEquipment = equipmentDbCategory.items.find((itemEquipment) => {
        return itemEquipment.id == itemComment.id;
      });
      itemComment.imgNames = itemEquipment.imgNames;
      itemComment.name = itemEquipment.name;
      itemComment.isCard = false;
      itemComment.categoryCode = categoryComment.categoryCode;
    });
    categoryComment.items = categoryComment.items.sort(
      (a, b) => b.star - a.star
    );

    categoryComment.cards.forEach((cardComment) => {
      const cardEquipment = equipmentDbCategory.cards.find((cardEquipment) => {
        return cardEquipment.id == cardComment.id;
      });
      cardComment.imgNames = cardEquipment.imgNames;
      cardComment.name = cardEquipment.name;
      cardComment.isCard = true;
      cardComment.categoryCode = categoryComment.categoryCode;
    });
    categoryComment.cards = categoryComment.cards.sort(
      (a, b) => b.star - a.star
    );

    categoryComment.hasCard = equipmentDbCategory.hasCard;
    categoryComment.noCardMessage = equipmentDbCategory.noCardMessage;
    categoryComment.category = equipmentDbCategory.category;
  });
}

function generateJson(guideDb) {
  let originalFormatGuideDb = common.deepClone(guideDb);
  originalFormatGuideDb.equipment.comments.forEach((category) => {
    delete category.category;
    delete category.hasCard;
    category.items.forEach((item) => {
      delete item.categoryCode;
      delete item.isCard;
      delete item.imgNames;
    });
    category.cards.forEach((card) => {
      delete card.categoryCode;
      delete card.isCard;
      delete card.imgNames;
    });
  });
  return JSON.stringify(originalFormatGuideDb);
}
