import { defineStore } from 'pinia';
import axios from 'axios';
import { logger } from '@/lib/logger';
import { common } from '@/lib/common';

export const useEquipmentStore = defineStore('equipment', {
  state: () => {
    return {
      equipment: {},
      equipmentTempDb: {},
    };
  },
  getters: {
    getEquipment: (state) => {
      return state.equipment;
    },
    getAllCategoryFromTempDb: (state) => {
      if (!state.equipmentTempDb.equipments) {
        return { equipments: [] };
      } else {
        return state.equipmentTempDb.equipments.map((category) => ({
          category: category.category,
          categoryCode: category.categoryCode,
          hasCard: category.hasCard,
          noCardMessage: category.noCardMessage,
        }));
      }
    },
    getEquipmentFromTempDb: (state) => {
      return (id, categoryCode, isCard) => {
        return findItem(state.equipmentTempDb, categoryCode, isCard, id);
      };
    },
    getEquipmentsFromTempDb: (state) => {
      return (name, categoryCode, isCard) => {
        return findItems(state.equipmentTempDb, name, categoryCode, isCard);
      };
    },
    getEquipmentWithCategoryFromTempDbByName: (state) => {
      return (name) => {
        return findItemsWithCategoryByName(state.equipmentTempDb, name);
      };
    },
    getEquipmentJsonFromTempDb: (state) => {
      return generateJson(state.equipmentTempDb);
    },
  },
  actions: {
    async fetchEquipment(id, categoryCode, isCard) {
      const responseEquipmentDb = (
        await axios.get(`${process.env.BASE_URL}database/equipments.json`)
      ).data;

      this.equipment = findItem(responseEquipmentDb, categoryCode, isCard, id);
    },
    async fetchEquipmentTempDb() {
      const responseEquipmentDb = (
        await axios.get(`${process.env.BASE_URL}database/equipments.json`)
      ).data;
      this.equipmentTempDb = responseEquipmentDb;
    },
    updateEquipmentTempDb(db) {
      this.equipmentTempDb = db;
    },
  },
});

function findItem(responseEquipmentDb, categoryCode, isCard, id) {
  const category = responseEquipmentDb.equipments.find((c) => {
    return c.categoryCode == categoryCode;
  });
  let dbItem;
  if (!isCard) {
    dbItem = category.items.find((item) => {
      return item.id == id;
    });
  } else {
    dbItem = category.cards.find((card) => {
      return card.id == id;
    });
  }
  dbItem.category = category.category;
  dbItem.categoryCode = category.categoryCode;
  dbItem.isCard = isCard;
  dbItem.relatedItems = findItemsByIds(responseEquipmentDb, dbItem.relatedIds);
  return dbItem;
}

function findItemsByIds(responseEquipmentDb, ids) {
  let finalItems = [];
  responseEquipmentDb.equipments.forEach((category) => {
    if (!ids || ids.length == 0) {
      return;
    }
    let items = category.items
      .filter((item) => {
        return ids.includes(item.id);
      })
      .map((item) => {
        return {
          id: item.id,
          name: item.name,
          slots: item.slots,
          categoryCode: category.categoryCode,
          isCard: false,
          imgNames: item.imgNames,
        };
      });
    finalItems = finalItems.concat(items);
    ids = ids.filter((id) => {
      return !items.map((item) => item.id).includes(id);
    });
    let cards = category.cards
      .filter((card) => {
        return ids.includes(card.id);
      })
      .map((card) => {
        return {
          id: card.id,
          name: card.name,
          categoryCode: category.categoryCode,
          isCard: true,
          imgNames: card.imgNames,
        };
      });
    finalItems = finalItems.concat(cards);
    ids = ids.filter((id) => {
      return !cards.map((card) => card.id).includes(id);
    });
  });
  return finalItems;
}

function findItems(responseEquipmentDb, name, categoryCode, isCard) {
  logger.info(name);
  if (!name) {
    return [];
  }
  let finalItems = [];
  responseEquipmentDb.equipments.forEach((category) => {
    if (categoryCode && categoryCode != category.categoryCode) {
      return;
    }
    if (isCard === undefined || isCard === false) {
      let items = category.items
        .filter((item) => {
          return item.name.indexOf(name) >= 0;
        })
        .map((item) => ({
          ...item,
          isCard: false,
          categoryCode: category.categoryCode,
        }));
      finalItems = finalItems.concat(items);
    }

    if (isCard === undefined || isCard === true) {
      let cards = category.cards
        .filter((card) => {
          return card.name.indexOf(name) >= 0;
        })
        .map((card) => ({
          ...card,
          isCard: true,
          categoryCode: category.categoryCode,
        }));
      finalItems = finalItems.concat(cards);
    }
  });
  return finalItems;
}

function findItemsWithCategoryByName(responseEquipmentDb, name) {
  if (!name) {
    return [];
  }
  let temp = common.deepClone(responseEquipmentDb.equipments);
  temp.forEach((category) => {
    category.items = category.items.filter((item) => {
      return item.name.indexOf(name) >= 0;
    });
    category.cards = category.cards.filter((card) => {
      return card.name.indexOf(name) >= 0;
    });
  });
  temp = temp.filter((category) => {
    return category.items.length > 0 || category.cards.length > 0;
  });
  return temp;
}

function generateJson(equipmentDb) {
  let temp = common.deepClone(equipmentDb);
  temp.equipments.forEach((category) => {
    category.items.forEach((item) => {
      delete item.relatedItems;
      delete item.isCard;
    });
    category.items = category.items.sort((a, b) => {
      return a.name.localeCompare(b.name);
    });
    category.cards.forEach((card) => {
      delete card.relatedItems;
      delete card.isCard;
    });
    category.cards = category.cards.sort((a, b) => {
      return a.name.localeCompare(b.name);
    });
  });
  return JSON.stringify(temp);
}
