<template>
  <v-card density="comfortable">
    <v-card-title class="bg-primary-light">{{ title }}</v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <!-- 裝備位置 -->
      <v-select
        :items="categories"
        v-model="item.categoryCode"
        item-title="category"
        item-value="categoryCode"
        label="裝備位置"
        density="comfortable"
        :disabled="props.editMode"
        hide-details="auto"
        class="my-3"
      ></v-select>

      <!-- 類別 -->
      <v-select
        :items="equipmentType"
        v-model="item.isCard"
        item-title="text"
        item-value="value"
        label="類別"
        density="comfortable"
        :disabled="props.editMode"
        hide-details="auto"
        class="my-3"
      ></v-select>

      <!-- ID -->
      <v-text-field
        label="ID"
        density="comfortable"
        v-model="item.id"
        readonly
        hide-details="auto"
        class="my-3"
      ></v-text-field>

      <!-- 圖片 -->
      <v-card density="comfortable" class="mb-10">
        <v-card-title class="bg-primary-light">圖片</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-slide-y-transition group>
            <v-card
              density="comfortable"
              v-for="(img, index) in item.imgNames"
              :key="index"
              class="my-4"
            >
              <v-card-title
                class="d-flex flex-row align-center justify-space-between bg-primary-light"
              >
                圖片{{ index + 1 }}
                <v-btn
                  color="error"
                  size="small"
                  variant="tonal"
                  @click="removeImage(index)"
                >
                  <v-icon>mdi-delete</v-icon>
                  刪除圖片
                </v-btn>
              </v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-text-field
                  label="檔案名稱"
                  density="comfortable"
                  v-model="item.imgNames[index]"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <div class="text-subtitle-2 mb-2">預覽圖</div>
                <v-img
                  :src="
                    filePath.getCommentItemImagePath(
                      item.imgNames[index],
                      item.categoryCode,
                      item.isCard
                    )
                  "
                  max-width="78"
                  :lazy-src="filePath.getNotFoundImagePath()"
                  class="rounded-sm me-3"
                >
                  <template v-slot:placeholder>
                    <div class="d-flex align-center justify-center fill-height">
                      <v-progress-circular
                        indeterminate
                        color="grey-lighten-4"
                      ></v-progress-circular>
                    </div>
                  </template>
                </v-img>
              </v-card-text>
            </v-card>
          </v-slide-y-transition>

          <v-btn
            color="secondary"
            size="small"
            variant="tonal"
            class="my-4"
            @click="addImage"
          >
            <v-icon>mdi-plus</v-icon>
            新增圖片
          </v-btn>
        </v-card-text>
      </v-card>

      <!-- 名稱 -->
      <v-text-field
        label="名稱"
        v-model="item.name"
        density="comfortable"
        hide-details="auto"
        class="my-3"
      ></v-text-field>

      <!-- 插槽數 -->
      <v-select
        :items="slots"
        v-model="item.slots"
        item-title="text"
        item-value="value"
        label="插槽數"
        density="comfortable"
        hide-details="auto"
        class="my-3"
      ></v-select>

      <!-- 是否實裝 -->
      <v-switch
        v-model="item.available"
        :label="`${item.available ? '已實裝' : '未實裝'}`"
        color="secondary"
        class="switch"
      ></v-switch>

      <!-- 取得方式 -->
      <v-text-field
        label="取得方式"
        v-model="item.loot"
        density="comfortable"
        hide-details="auto"
        class="my-3"
      ></v-text-field>

      <!-- 相關連結 -->
      <v-card density="comfortable" class="my-4">
        <v-card-title class="bg-primary-light">相關連結</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-slide-y-transition group>
            <v-card
              density="comfortable"
              v-for="(externalUrl, index) in item.externalUrls"
              :key="index"
              class="my-4"
            >
              <v-card-title
                class="d-flex flex-row align-center justify-space-between bg-primary-light"
              >
                第{{ index + 1 }}組
                <v-btn
                  color="error"
                  size="small"
                  variant="tonal"
                  @click="removeExternalUrl(index)"
                >
                  <v-icon>mdi-delete</v-icon>
                  刪除連結
                </v-btn>
              </v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-text-field
                  label="顯示文字"
                  v-model="externalUrl.name"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-text-field
                  label="URL"
                  v-model="externalUrl.url"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
              </v-card-text>
            </v-card>
          </v-slide-y-transition>
          <v-btn
            color="secondary"
            size="small"
            variant="tonal"
            class="my-4"
            @click="addExternalUrl"
          >
            <v-icon>mdi-plus</v-icon>
            新增連結
          </v-btn>
        </v-card-text>
      </v-card>

      <!-- 詳細資訊 -->
      <v-card density="comfortable" class="my-4">
        <v-card-title class="bg-primary-light">詳細資訊</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-slide-y-transition group>
            <v-card
              density="comfortable"
              v-for="(info, indexInfo) in item.infos"
              :key="indexInfo"
              class="my-4"
            >
              <v-card-title
                class="d-flex flex-row align-center justify-space-between bg-primary-light"
              >
                組件{{ indexInfo + 1 }}
                <v-btn
                  color="error"
                  size="small"
                  @click="removeInfo(indexInfo)"
                  variant="tonal"
                >
                  <v-icon>mdi-delete</v-icon>
                  刪除組件
                </v-btn>
              </v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-textarea
                  label="說明"
                  v-model="infoContents[indexInfo]"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-textarea>
                <v-text-field
                  label="位置"
                  v-model="info.position"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-text-field
                  :label="categoryCode == 'weapon' ? '攻擊' : '防禦'"
                  v-model="info.value"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-text-field
                  label="重量"
                  v-model="info.weight"
                  type="number"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-text-field
                  label="屬性"
                  v-model="info.element"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-select
                  :items="equipmentLevel"
                  v-model="info.equipmentLevel"
                  item-title="text"
                  item-value="value"
                  label="裝備等級"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-select>
                <v-text-field
                  label="要求等級"
                  v-model="info.requiresLevel"
                  type="number"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-text-field
                  label="裝備職業"
                  v-model="info.usableBy"
                  density="comfortable"
                  hide-details="auto"
                  class="my-3"
                ></v-text-field>
                <v-card density="comfortable" class="my-4" v-if="!props.isCard">
                  <v-card-title class="bg-primary-light">附魔</v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    <v-slide-y-transition group>
                      <v-card
                        density="comfortable"
                        v-for="(
                          enchantment, indexEnchantment
                        ) in info.enchantments"
                        :key="indexEnchantment"
                        class="my-4"
                      >
                        <v-card-title class="bg-primary-light">
                          第{{ indexEnchantment + 1 }}條
                        </v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                          <v-text-field
                            label="附魔名稱"
                            v-model="enchantment.name"
                            density="comfortable"
                            hide-details="auto"
                            class="my-3"
                          ></v-text-field>
                          <v-textarea
                            label="詳細說明"
                            v-model="
                              enchantmentDetails[indexInfo][indexEnchantment]
                            "
                            density="comfortable"
                            hide-details="auto"
                            class="my-3"
                          ></v-textarea>
                        </v-card-text>
                      </v-card>
                    </v-slide-y-transition>
                    <v-btn
                      color="secondary"
                      size="small"
                      variant="tonal"
                      class="my-4"
                      @click="addEnchantment(info.enchantments)"
                    >
                      <v-icon>mdi-plus</v-icon>
                      新增附魔
                    </v-btn>
                  </v-card-text>
                </v-card>
              </v-card-text>
            </v-card>
          </v-slide-y-transition>
          <v-btn
            color="secondary"
            size="small"
            variant="tonal"
            class="my-4"
            @click="addInfo"
          >
            <v-icon>mdi-plus</v-icon>
            新增組件
          </v-btn>
        </v-card-text>
      </v-card>

      <!-- 相關套裝 -->
      <v-card density="comfortable" class="my-4">
        <v-card-title class="bg-primary-light">相關套裝</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-text-field
            label="裝備名稱"
            v-model="relatedItem.searchCondition"
            @update:modelValue="findItems"
            density="comfortable"
            prepend-inner-icon="mdi-magnify"
          ></v-text-field>
          <v-table
            v-if="relatedItem.searchResult.length > 0"
            height="300px"
            class="mt-0"
          >
            <thead>
              <tr>
                <th class="text-left">裝備名稱</th>
                <th class="text-left">加入關聯</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(equipment, index) in relatedItem.searchResult"
                :key="index"
              >
                <td>{{ equipment.name }}</td>
                <td>
                  <v-btn
                    color="secondary"
                    size="small"
                    variant="tonal"
                    class="my-4"
                    @click="addRelatedItem(equipment)"
                  >
                    <v-icon>mdi-plus</v-icon>
                    加入套裝
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-table>

          <v-list>
            <v-list-subheader>已連結裝備</v-list-subheader>
            <v-list-item
              v-if="relatedItem.selectedItems.length == 0"
              class="text-overline"
            >
              目前無連結裝備
            </v-list-item>
            <v-slide-y-transition group>
              <v-list-item
                v-for="(selectedItem, index) in relatedItem.selectedItems"
                :key="index"
                :title="selectedItem.name"
                :subtitle="selectedItem.id"
              >
                <template v-slot:prepend>
                  <v-img
                    :src="
                      filePath.getCommentItemImagePath(
                        selectedItem.imgNames[0],
                        selectedItem.categoryCode,
                        selectedItem.isCard
                      )
                    "
                    width="30"
                    max-width="30"
                    class="rounded-sm me-3"
                  ></v-img>
                </template>

                <template v-slot:append>
                  <v-btn
                    color="error"
                    size="small"
                    variant="tonal"
                    @click="removeRelatedItem(index)"
                  >
                    <v-icon>mdi-delete</v-icon>
                    刪除套裝
                  </v-btn>
                </template>
              </v-list-item>
            </v-slide-y-transition>
          </v-list>
        </v-card-text>
      </v-card>
    </v-card-text>
    <v-card-actions>
      <v-btn variant="tonal" @click="save" color="green" block>Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script setup>
import { logger } from '@/lib/logger';
import { ref, watch, onMounted } from 'vue';
import { useEquipmentStore } from '@/store/equipment';
import { filePath } from '@/lib/filepath';
import { common } from '@/lib/common';

const title = ref('');

const slots = ref([
  { text: '0', value: 0 },
  { text: '1', value: 1 },
  { text: '2', value: 2 },
  { text: '3', value: 3 },
  { text: '4', value: 4 },
]);
const equipmentLevel = ref([
  { text: '1', value: 1 },
  { text: '2', value: 2 },
  { text: '3', value: 3 },
  { text: '4', value: 4 },
]);

const categories = ref([]);
const selectedCategoryCode = ref('');

const equipmentType = ref([
  { text: '裝備', value: false },
  { text: '卡片', value: true },
]);

const originalItem = ref({});
const item = ref({});
const infoContents = ref([]);
const enchantmentDetails = ref([]);
const relatedItem = ref({
  searchCondition: '',
  searchResult: [],
  selectedItems: [],
});

const props = defineProps(['id', 'categoryCode', 'isCard', 'editMode']);
const emit = defineEmits(['save']);

const equipmentStore = useEquipmentStore();

watch(
  () => [props.id, props.editMode],
  async () => {
    if (props.editMode) {
      title.value = '編輯';
      originalItem.value = equipmentStore.getEquipmentFromTempDb(
        props.id,
        props.categoryCode,
        props.isCard
      );

      item.value = common.deepClone(originalItem.value);
      infoContents.value = item.value.infos.map((info) =>
        info.contents.join('\n')
      );

      if (!props.isCard) {
        enchantmentDetails.value = item.value.infos.map((info) =>
          info.enchantments.map((enchantment) => enchantment.detail.join('\n'))
        );
      }

      if (item.value.relatedItems) {
        relatedItem.value.selectedItems = JSON.parse(
          JSON.stringify(item.value.relatedItems)
        );
      }
    } else {
      title.value = '新增';
      item.value = {};
      item.value.id = props.id;
      item.value.externalUrls = [];
      item.value.imgNames = [];
      item.value.infos = [];
      item.value.relatedIds = [];
      relatedItem.value = {
        searchCondition: '',
        searchResult: [],
        selectedItems: [],
      };
      infoContents.value = [];
      enchantmentDetails.value = [];
    }
    categories.value = equipmentStore.getAllCategoryFromTempDb;
  },
  { immediate: true }
);

function addImage() {
  if (!item.value.imgNames) {
    item.value.imgNames = [];
  }
  item.value.imgNames.push('');
}
function removeImage(index) {
  item.value.imgNames.splice(index, 1);
}

function addExternalUrl() {
  if (!item.value.externalUrls) {
    item.value.externalUrls = [];
  }
  item.value.externalUrls.push({ name: '', url: '' });
}
function removeExternalUrl(index) {
  item.value.externalUrls.splice(index, 1);
}

function addInfo() {
  if (!item.value.infos) {
    item.value.infos = [];
  }
  item.value.infos.push({ enchantments: [] });
  infoContents.value.push('');
  enchantmentDetails.value.push([]);
}
function removeInfo(index) {
  item.value.infos.splice(index, 1);
  infoContents.value.splice(index, 1);
}

function addEnchantment(enchantments) {
  enchantments.push({
    name: '',
    detail: [],
  });
}

async function findItems() {
  if (relatedItem.value.searchCondition) {
    let foundItems = equipmentStore.getEquipmentsFromTempDb(
      relatedItem.value.searchCondition,
      '',
      undefined
    );
    relatedItem.value.searchResult = foundItems;
  } else {
    relatedItem.value.searchResult = [];
  }
}

function addRelatedItem(equipment) {
  if (!item.value.relatedIds) {
    item.value.relatedIds = [];
  }
  if (
    !item.value.relatedIds.includes(equipment.id) &&
    equipment.id != item.value.id
  ) {
    item.value.relatedIds.push(equipment.id);
    relatedItem.value.selectedItems.push(equipment);
  }
}

function removeRelatedItem(index) {
  item.value.relatedIds.splice(index, 1);
  relatedItem.value.selectedItems.splice(index, 1);
}

function save() {
  item.value.infos.forEach((info, index) => {
    logger.info(infoContents.value);
    info.contents = infoContents.value[index].split('\n');
  });

  emit(
    'save',
    item.value,
    originalItem.value.relatedIds,
    item.value.categoryCode,
    item.value.isCard,
    props.editMode
  );
}
</script>
<style scoped>
.switch {
  width: 100px;
}
</style>
