<template>
  <div>
    <v-card density="comfortable" class="mb-2">
      <v-card-title class="bg-primary-light">攻略編輯</v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <v-expansion-panels class="mb-2">
          <v-expansion-panel>
            <v-expansion-panel-title>文章內容</v-expansion-panel-title>
            <v-expansion-panel-text>
              <!-- 標題 -->
              <v-text-field
                v-model="guideDb.title"
                class="my-3 rounded-text-field"
                label="標題"
                density="comfortable"
                hide-details="auto"
              ></v-text-field>

              <!-- Icon -->
              <v-text-field
                v-model="guideDb.icon"
                class="my-3"
                label="Icon"
                density="comfortable"
                hide-details="auto"
              ></v-text-field>

              <!-- 介紹 -->
              <v-card density="comfortable" class="my-4">
                <v-card-title class="bg-primary-light">介紹</v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <!-- 章節標題 -->
                  <v-text-field
                    v-model="guideDb.introduction.title"
                    class="my-3"
                    label="章節標題"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                  <!-- 章節子標題 -->
                  <v-text-field
                    class="my-3"
                    label="章節子標題"
                    density="comfortable"
                    hide-details="auto"
                    v-model="guideDb.introduction.subTitle"
                  ></v-text-field>
                  <!-- 內文 -->
                  <v-textarea
                    class="my-3"
                    label="內文"
                    density="comfortable"
                    hide-details="auto"
                    v-model="guideDb.introduction.content"
                  ></v-textarea>
                </v-card-text>
              </v-card>

              <!-- 配點 -->
              <v-card density="comfortable" class="my-4">
                <v-card-title class="bg-primary-light">配點</v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <!-- 章節標題 -->
                  <v-text-field
                    class="my-3"
                    label="章節標題"
                    density="comfortable"
                    hide-details="auto"
                    v-model="guideDb.build.title"
                  ></v-text-field>
                  <!-- 章節子標題 -->
                  <v-text-field
                    class="my-3"
                    label="章節子標題"
                    density="comfortable"
                    hide-details="auto"
                    v-model="guideDb.build.subTitle"
                  ></v-text-field>
                  <!-- 技能配點 -->
                  <v-card density="comfortable" class="my-4">
                    <v-card-title class="bg-primary-light">技能</v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                      <!-- 章節標題 -->
                      <v-text-field
                        class="my-3"
                        label="章節標題"
                        density="comfortable"
                        hide-details="auto"
                        v-model="guideDb.build.skill.title"
                      ></v-text-field>
                      <!-- 章節子標題 -->
                      <v-text-field
                        class="my-3"
                        label="章節子標題"
                        density="comfortable"
                        hide-details="auto"
                        v-model="guideDb.build.skill.subTitle"
                      ></v-text-field>
                      <!-- 內文 -->
                      <v-textarea
                        class="my-3"
                        label="內文"
                        density="comfortable"
                        v-model="guideDb.build.skill.content"
                        hide-details="auto"
                      ></v-textarea>
                      <v-card
                        density="comfortable"
                        class="my-4"
                        v-for="(info, indexSkill) in guideDb.build.skill.infos"
                        :key="indexSkill"
                      >
                        <v-card-title
                          class="d-flex flex-row align-center justify-space-between bg-primary-light"
                        >
                          技能{{ indexSkill + 1 }}
                          <v-btn
                            size="small"
                            color="error"
                            variant="tonal"
                            @click="removeSkillInfo(indexSkill)"
                          >
                            <v-icon>mdi-delete</v-icon>
                            刪除技能
                          </v-btn>
                        </v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                          <!-- 技能名稱 -->
                          <v-text-field
                            class="my-3"
                            label="技能名稱"
                            density="comfortable"
                            hide-details="auto"
                            v-model="info.name"
                          ></v-text-field>
                          <!-- Icon -->
                          <v-text-field
                            class="my-3"
                            label="Icon"
                            density="comfortable"
                            hide-details="auto"
                            v-model="info.icon"
                          ></v-text-field>
                          <!-- 說明 -->
                          <v-textarea
                            class="my-3"
                            hide-details="auto"
                            label="說明"
                            v-model="info.description"
                          ></v-textarea>
                          <!-- 公式 -->
                          <v-textarea
                            class="my-3"
                            label="公式"
                            hide-details="auto"
                            v-model="info.formula"
                          ></v-textarea>
                          <!-- 等級 -->
                          <v-list>
                            <v-list-item
                              v-for="(level, indexSkillLevel) in info.level"
                              :key="indexSkillLevel"
                              class="mx-0 px-0"
                            >
                              <v-text-field
                                :label="`Lv.${indexSkillLevel + 1}`"
                                density="comfortable"
                                v-model="info.level[indexSkillLevel]"
                                hide-details="auto"
                                class="my-1"
                              ></v-text-field>
                              <template v-slot:append>
                                <v-btn
                                  class="mx-2"
                                  size="small"
                                  color="error"
                                  variant="tonal"
                                  @click="
                                    removeSkillInfoLevel(
                                      indexSkill,
                                      indexSkillLevel
                                    )
                                  "
                                >
                                  <v-icon>mdi-delete</v-icon>
                                  刪除等級
                                </v-btn>
                              </template>
                            </v-list-item>
                            <v-list-item>
                              <v-btn
                                color="secondary"
                                size="small"
                                variant="tonal"
                                class="my-4"
                                @click="addSkillInfoLevel(equipment)"
                              >
                                <v-icon>mdi-plus</v-icon>
                                新增等級
                              </v-btn>
                            </v-list-item>
                          </v-list>
                          <v-textarea
                            class="my-3"
                            label="參考"
                            density="comfortable"
                            v-model="info.reference"
                          ></v-textarea>
                        </v-card-text>
                      </v-card>
                    </v-card-text>
                  </v-card>

                  <!-- 素質配點 -->
                  <v-card density="comfortable" class="my-4">
                    <v-card-title class="bg-primary-light">素質</v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                      <!-- 章節標題 -->
                      <v-text-field
                        class="my-3"
                        hide-details="auto"
                        label="章節標題"
                        density="comfortable"
                        v-model="guideDb.build.state.title"
                      ></v-text-field>
                      <!-- 章節子標題 -->
                      <v-text-field
                        class="my-3"
                        label="章節子標題"
                        density="comfortable"
                        hide-details="auto"
                        v-model="guideDb.build.state.subTitle"
                      ></v-text-field>
                      <!-- 內文 -->
                      <v-textarea
                        class="my-3"
                        label="內文"
                        v-model="guideDb.build.state.content"
                        hide-details="auto"
                      ></v-textarea>
                      <!-- 備註 -->
                      <v-text-field
                        class="my-3"
                        label="備註"
                        density="comfortable"
                        hide-details="auto"
                        v-model="guideDb.build.state.remark"
                      ></v-text-field>
                    </v-card-text>
                  </v-card>
                </v-card-text>
              </v-card>
            </v-expansion-panel-text>
          </v-expansion-panel>
        </v-expansion-panels>

        <v-card density="comfortable">
          <v-card-title class="bg-primary-light">裝備評論</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <div
              class="d-flex"
              :class="{ 'flex-column': commonStore.isMobileMode }"
            >
              <v-card
                density="comfortable"
                class="flex-grow-0 flex-shrink-1"
                :class="{
                  'my-1': commonStore.isMobileMode,
                  'mx-1': !commonStore.isMobileMode,
                }"
              >
                <v-card-title class="bg-primary-light">部位</v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <v-list nav v-if="!_.isEmpty(guideDb)">
                    <v-list-item
                      v-for="category in guideDb.equipment.comments"
                      :key="category.categoryCode"
                      :title="category.category"
                      :subtitle="category.categoryCode"
                      :value="category.categoryCode"
                      active-color="secondary"
                      @click="showEquipmentList(category)"
                      :active="
                        !_.isEmpty(commentSettings.selectedCategory) &&
                        commentSettings.selectedCategory.categoryCode ==
                          category.categoryCode
                      "
                    ></v-list-item>
                  </v-list>
                </v-card-text>
              </v-card>
              <v-slide-x-transition>
                <v-card
                  density="comfortable"
                  class="flex-grow-0 flex-shrink-1"
                  :class="{
                    'my-1': commonStore.isMobileMode,
                    'mx-1': !commonStore.isMobileMode,
                  }"
                  v-if="!_.isEmpty(commentSettings.selectedCategory)"
                >
                  <v-card-title class="bg-primary-light">裝備</v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    <v-list nav>
                      <div class="text-subtitle-1 text-medium-emphasis">
                        裝備
                      </div>

                      <v-list-item
                        v-for="item in commentSettings.selectedCategory.items"
                        :key="item.id"
                        :title="item.name"
                        :value="item"
                        active-color="secondary"
                        @click="editComment(item)"
                        :active="commentSettings.selectedItem.id == item.id"
                      >
                        <template v-slot:prepend>
                          <v-img
                            :src="
                              filePath.getCommentItemImagePath(
                                item.imgNames[0],
                                item.categoryCode,
                                false
                              )
                            "
                            width="30"
                            max-width="30"
                            class="rounded-sm me-3"
                          ></v-img>
                        </template>
                        <template v-slot:subtitle>
                          <v-rating
                            size="x-small"
                            density="compact"
                            color="orange"
                            v-model="item.star"
                            half-increments
                            readonly
                          ></v-rating>
                        </template>
                      </v-list-item>
                      <v-list-item
                        value="new-item"
                        title="新增裝備"
                        active-color="secondary"
                        @click="
                          addComment(
                            commentSettings.selectedCategory.categoryCode,
                            false
                          )
                        "
                        :active="commentSettings.selectedItem.id == 'new-item'"
                      >
                        <template v-slot:prepend>
                          <v-icon class="v-list-item-add-icon">mdi-plus</v-icon>
                        </template>
                      </v-list-item>
                      <template
                        v-if="
                          commentSettings.selectedCategory.hasCard &&
                          commentSettings.selectedCategory.noCardMessage ===
                            undefined
                        "
                      >
                        <div class="text-subtitle-1 text-medium-emphasis">
                          卡片
                        </div>
                        <v-list-item
                          v-for="card in commentSettings.selectedCategory.cards"
                          :key="card.id"
                          :title="card.name"
                          :subtitle="card.id"
                          :value="card"
                          active-color="secondary"
                          @click="editComment(card)"
                          :active="commentSettings.selectedItem.id == card.id"
                        >
                          <template v-slot:prepend>
                            <v-img
                              :src="
                                filePath.getCommentItemImagePath(
                                  card.imgNames[0],
                                  card.categoryCode,
                                  true
                                )
                              "
                              width="30"
                              max-width="30"
                              class="rounded-sm me-3"
                            ></v-img>
                          </template>
                          <template v-slot:subtitle>
                            <v-rating
                              size="x-small"
                              density="compact"
                              color="orange"
                              v-model="card.star"
                              half-increments
                              readonly
                            ></v-rating>
                          </template>
                        </v-list-item>

                        <v-list-item
                          value="new-card"
                          title="新增卡片"
                          active-color="secondary"
                          @click="
                            addComment(
                              commentSettings.selectedCategory.categoryCode,
                              true
                            )
                          "
                          :active="
                            commentSettings.selectedItem.id == 'new-card'
                          "
                        >
                          <template v-slot:prepend>
                            <v-icon class="v-list-item-add-icon">
                              mdi-plus
                            </v-icon>
                          </template>
                        </v-list-item>
                      </template>
                    </v-list>
                  </v-card-text>
                </v-card>
              </v-slide-x-transition>
              <v-slide-x-transition>
                <v-card
                  density="comfortable"
                  class="flex-grow-1 flex-shrink-0"
                  :class="{
                    'my-1': commonStore.isMobileMode,
                    'mx-1': !commonStore.isMobileMode,
                  }"
                  v-if="
                    !_.isEmpty(commentSettings.selectedItem) ||
                    !commentSettings.editMode
                  "
                >
                  <v-card-title
                    class="d-flex flex-row align-center justify-space-between bg-primary-light"
                  >
                    評論
                    <v-btn
                      v-if="commentSettings.editMode"
                      size="small"
                      color="error"
                      variant="tonal"
                      @click="removeEquipmentComment"
                    >
                      <v-icon>mdi-delete</v-icon>
                      刪除評論
                    </v-btn>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    <v-card
                      density="comfortable"
                      v-if="!commentSettings.editMode"
                      class="mb-2"
                    >
                      <v-card-title class="bg-primary-light">搜尋</v-card-title>
                      <v-divider></v-divider>
                      <v-card-text>
                        <v-text-field
                          label="裝備名稱"
                          v-model="commentSettings.selectedItem.name"
                          @update:modelValue="
                            findEquipmentFromTempDbByNameAndCategory
                          "
                          density="comfortable"
                          prepend-inner-icon="mdi-magnify"
                        ></v-text-field>
                        <v-card-subtitle>搜尋結果</v-card-subtitle>
                        <v-list nav>
                          <v-list-item
                            v-if="commentSettings.foundItems.length == 0"
                            class="text-overline"
                          >
                            目前無搜尋結果
                          </v-list-item>
                          <v-list-item
                            v-for="item in commentSettings.foundItems"
                            :key="item.id"
                            :title="item.name"
                            :subtitle="item.id"
                            :value="item.id"
                            active-color="secondary"
                            @click.stop="selectNewItem(item)"
                          >
                            <template v-slot:prepend>
                              <v-img
                                :src="
                                  filePath.getCommentItemImagePath(
                                    item.imgNames[0],
                                    item.categoryCode,
                                    item.isCard
                                  )
                                "
                                width="30"
                                max-width="30"
                                class="rounded-sm me-3"
                              ></v-img>
                            </template>
                          </v-list-item>
                        </v-list>
                      </v-card-text>
                    </v-card>

                    <div
                      v-if="uuidValidate(commentSettings.selectedItem.id)"
                      class="d-flex align-center"
                    >
                      <v-img
                        :src="
                          filePath.getCommentItemImagePath(
                            commentSettings.selectedItem.imgNames[0],
                            commentSettings.selectedItem.categoryCode,
                            commentSettings.selectedItem.isCard
                          )
                        "
                        max-width="78"
                        class="rounded-sm me-3"
                      ></v-img>
                      <div>
                        {{
                          `${
                            commentSettings.selectedItem.name
                          }${equipment.getNameWithSlots(
                            commentSettings.selectedItem.slots
                          )}`
                        }}
                      </div>
                    </div>
                    <div class="d-flex align-center mb-2">
                      <div class="pt-1">推薦度:</div>
                      <v-rating
                        v-model="commentSettings.star"
                        hover
                        half-increments
                        color="orange"
                        density="compact"
                      ></v-rating>
                    </div>

                    <v-textarea
                      label="評價"
                      v-model="commentSettings.content"
                    ></v-textarea>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn
                      variant="tonal"
                      @click="saveEquipmentComment"
                      color="green"
                      block
                    >
                      <v-icon>mdi-content-save</v-icon>
                      儲存
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-slide-x-transition>
            </div>
          </v-card-text>
        </v-card>
      </v-card-text>
      <v-card-actions>
        <v-btn variant="tonal" @click="save" color="green" block>儲存</v-btn>
      </v-card-actions>
    </v-card>
    <v-slide-y-transition>
      <v-card density="comfortable" class="mt-2">
        <v-card-title class="bg-primary-light">輸出</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <div class="d-flex flex-row mb-3">
            <v-btn
              variant="tonal"
              color="secondary"
              @click="generateJson"
              class="me-2"
            >
              <v-icon class="me-1">mdi-code-json</v-icon>
              產生 JSON
            </v-btn>
            <v-btn variant="tonal" color="secondary" @click="copyJson">
              <v-icon class="me-1">mdi-content-copy</v-icon>
              複製 JSON
            </v-btn>
          </div>
          <v-textarea label="JSON" v-model="json" readonly></v-textarea>
        </v-card-text>
      </v-card>
    </v-slide-y-transition>
    <v-slide-y-transition>
      <v-alert
        v-if="alert.display"
        class="alert"
        :type="alert.type"
        density="compact"
        variant="tonal"
        closable
      >
        {{ alert.text }}
      </v-alert>
    </v-slide-y-transition>
  </div>
</template>
<script setup>
import { ref, onMounted, watch } from 'vue';
import { useCommonStore } from '@/store/common';
import { useEquipmentStore } from '@/store/equipment';
import { useGuideStore } from '@/store/guide';
import { filePath } from '@/lib/filepath';
import { common } from '@/lib/common';
import { equipment } from '@/lib/equipment';
import { validate as uuidValidate } from 'uuid';
import { openDialog } from 'vue3-promise-dialog';
import ConfirmDialog from '@/components/admin/ConfirmDialog.vue';
import _ from 'lodash';

const guideDb = ref({});
const commentSettings = ref({
  selectedCategory: {},
  selectedItem: {},
  editMode: true,
  foundItems: [],
  content: '',
  star: 0,
});
const json = ref('');
const alert = ref({
  display: false,
  type: 'success',
  text: '',
});

const props = defineProps(['classType']);

const commonStore = useCommonStore();
const equipmentStore = useEquipmentStore();
const guideStore = useGuideStore();

onMounted(async () => {
  await equipmentStore.fetchEquipmentTempDb();
});

watch(
  () => props.classType,
  async () => {
    await guideStore.fetchGuideTempDb(props.classType);
    let db = common.deepClone(guideStore.getGuideTempDb(props.classType));
    CovertGuideContentToString(db);
    guideDb.value = db;
    resetComment();
    resetCategory();
  },
  { immediate: true }
);

watch(
  () => commentSettings.value.selectedCategory,
  () => {
    resetComment();
  }
);

function CovertGuideContentToString(guide) {
  guide.introduction.content = guide.introduction.content.join('\n');
  guide.build.skill.content = guide.build.skill.content.join('\n');
  guide.build.skill.infos.forEach((info) => {
    info.description = info.description.join('\n');
    info.formula = info.formula.join('\n');
    info.reference = info.reference.join('\n');
  });
  guide.build.state.content = guide.build.state.content.join('\n');
  guide.equipment.content = guide.equipment.content.join('\n');
}

function CovertGuideContentToArray(guide) {
  guide.introduction.content = guide.introduction.content.split('\n');
  guide.build.skill.content = guide.build.skill.content.split('\n');
  guide.build.skill.infos.forEach((info) => {
    info.description = info.description.split('\n');
    info.formula = info.formula.split('\n');
    info.reference = info.reference.split('\n');
  });
  guide.build.state.content = guide.build.state.content.split('\n');
  guide.equipment.content = guide.equipment.content.split('\n');
}

function showEquipmentList(category) {
  commentSettings.value.selectedCategory = category;
}

function editComment(item) {
  commentSettings.value.selectedItem = item;
  commentSettings.value.editMode = true;
  commentSettings.value.content = item.comments.join('\n');
  commentSettings.value.star = item.star;
}

function addComment(categoryCode, isCard) {
  commentSettings.value.editMode = false;
  commentSettings.value.content = '';
  commentSettings.value.star = 0;
  commentSettings.value.selectedItem = {
    id: !isCard ? 'new-item' : 'new-card',
    categoryCode: categoryCode,
    isCard: isCard,
  };
}

function findEquipmentFromTempDbByNameAndCategory() {
  commentSettings.value.foundItems = equipmentStore.getEquipmentsFromTempDb(
    commentSettings.value.selectedItem.name,
    commentSettings.value.selectedItem.categoryCode,
    commentSettings.value.selectedItem.isCard
  );
}

function selectNewItem(item) {
  commentSettings.value.selectedItem = item;
}

function resetComment() {
  commentSettings.value.selectedItem = {};
  commentSettings.value.foundItems = [];
  commentSettings.value.content = '';
  commentSettings.value.star = 0;
  commentSettings.value.editMode = true;
}

function resetCategory() {
  commentSettings.value.selectedCategory = {};
}

function saveEquipmentComment() {
  let targetCategory = guideDb.value.equipment.comments.find((c) => {
    return c.categoryCode == commentSettings.value.selectedItem.categoryCode;
  });

  if (commentSettings.value.editMode) {
    let targetItem;
    if (!commentSettings.value.selectedItem.isCard) {
      targetItem = targetCategory.items.find((i) => {
        return i.id == commentSettings.value.selectedItem.id;
      });
    } else {
      targetItem = targetCategory.cards.find((c) => {
        return c.id == commentSettings.value.selectedItem.id;
      });
    }
    targetItem.comments = commentSettings.value.content.split('\n');
    targetItem.star = commentSettings.value.star;
    if (!commentSettings.value.selectedItem.isCard) {
      sortItems(targetCategory.items);
    } else {
      sortItems(targetCategory.cards);
    }
  } else {
    const item = {
      id: commentSettings.value.selectedItem.id,
      star: commentSettings.value.star,
      comments: commentSettings.value.content.split('\n'),
      categoryCode: commentSettings.value.selectedItem.categoryCode,
      isCard: commentSettings.value.selectedItem.isCard,
      name: commentSettings.value.selectedItem.name,
      imgNames: commentSettings.value.selectedItem.imgNames,
    };
    if (!commentSettings.value.selectedItem.isCard) {
      if (
        targetCategory.items.find((i) => {
          return i.id == item.id;
        })
      ) {
        showAlert('該裝備已存在!', 'error');
        return;
      }
      targetCategory.items.push(item);
    } else {
      if (
        targetCategory.cards.find((c) => {
          return c.id == item.id;
        })
      ) {
        showAlert('該卡片已存在!', 'error');
        return;
      }
      targetCategory.cards.push(item);
    }
    if (!commentSettings.value.selectedItem.isCard) {
      sortItems(targetCategory.items);
    } else {
      sortItems(targetCategory.cards);
    }
    resetComment();
  }
  showAlert('儲存成功!', 'success');
}

async function removeEquipmentComment() {
  const confirm = await openDialog(ConfirmDialog, {
    message: '是否確定刪除?',
    type: 'error',
  });
  if (!confirm) {
    return;
  }
  let targetCategory = guideDb.value.equipment.comments.find((c) => {
    return c.categoryCode == commentSettings.value.selectedItem.categoryCode;
  });

  if (!commentSettings.value.selectedItem.isCard) {
    targetCategory.items = targetCategory.items.filter((i) => {
      return i.id != commentSettings.value.selectedItem.id;
    });
  } else {
    targetCategory.cards = targetCategory.cards.filter((c) => {
      return c.id != commentSettings.value.selectedItem.id;
    });
  }
  resetComment();
}

async function save() {
  CovertGuideContentToArray(guideDb.value);
  await guideStore.updateGuideTempDb(props.classType, guideDb.value);
  showAlert('儲存成功!', 'success');
}

function sortItems(items) {
  items = items.sort((a, b) => {
    return b.star - a.star;
  });
}

function generateJson() {
  json.value = guideStore.getGuideJsonFromTempDb(props.classType);
}

function copyJson() {
  navigator.clipboard.writeText(json.value);
  showAlert('複製成功', 'success');
}

var timeout;
function showAlert(text, type) {
  clearTimeout(timeout);
  alert.value.text = text;
  alert.value.type = type;
  alert.value.display = true;
  timeout = setTimeout(() => {
    alert.value.display = false;
  }, 2000);
}
</script>

