<template>
  <div>
    <v-dialog v-model="dialog" persistent>
      <v-card>
        <v-card-text>{{ message }}</v-card-text>
        <v-card-actions>
          <v-btn variant="tonal" :color="type" @click="closeDialog()">
            確定
          </v-btn>
          <v-btn variant="tonal" @click="closeDialog(false)">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { defineComponent, ref } from 'vue';
import { closeDialog } from 'vue3-promise-dialog';

export default defineComponent({
  props: ['message', 'type'],
  setup() {
    const dialog = ref(true);
    function returnValue() {
      return true;
    }
    return {
      returnValue,
      closeDialog,
      dialog,
    };
  },
});
</script>
<style lang=""></style>
